const images = [
    {
        url: `${process.env.PUBLIC_URL}/assets/homeGallery/1.jpg`
    },
    {
        url: `${process.env.PUBLIC_URL}/assets/homeGallery/2.jpg`
    },
    {
        url: `${process.env.PUBLIC_URL}/assets/homeGallery/3.jpg`
    },
    {
        url: `${process.env.PUBLIC_URL}/assets/homeGallery/4.jpg`
    },
    {
        url: `${process.env.PUBLIC_URL}/assets/homeGallery/5.jpg`
    }
]

function BackgroundChanger() {
    return (
        <div
            className='homeGallery'

        >
            {/* <motion.div
                className='galleryImagesContainer'
                variants={backgroundChangerVariants}
                // initial={{x: "0"}}
                animate="animate"
                transition={{
                    duration: 45,
                    // times: [0, 5, 10, 15],
                    type: 'tween'
                }}
            >
                {images.map((i, index) => (
                    <img
                        alt="background"
                        src={i.url}
                    />
                ))} */}


            {images.map((i, index) => (
                <motion.img
                    className={`galleryImg-${index}`}
                    alt="background"
                    src={i.url}
                    initial={{
                        opacity: 1
                    }}
                    animate={{
                        opacity: 0
                    }}
                    transition={{
                        delay: 5 + (5 * index),
                        duration: 2
                    }}
                    style={{
                        zIndex: -index
                    }}                    
                />
            ))}

            <img
             src={`${process.env.PUBLIC_URL}/assets/homeGallery/6.jpg`}
            className="galleryImg-6"
            alt="background"
            style={{
                zIndex: -5
            }}
            />
        </div>
    )
}

export default BackgroundChanger